.wallet-sidebar-header {position: relative; padding-left: 30px; padding: 10px 15px 10px 45px; cursor: pointer; border-bottom: 1px solid #222227; }
.wallet-sidebar-header h4 {font-size: 16px; margin: 0px; padding: 0px;}
.wallet-sidebar-header p {font-size: 10px; margin: 0px; padding: 0px; }
.wallet-sidebar-header-left {position: absolute; left: 15px; top: 13px;}
.wallet-sidebar-balance {text-align: center;}
.wallet-sidebar-balance button {background:#6164ff; border:0 none; color: #fff; height: 40px; line-height: 40px; width: 100%; border-radius: 10px;}
.wallet-sidebar-balance h5 {font-size: 15px;}
.wallet-sidebar-balance p {font-weight: 600; margin: 15px 0;}

.hero__profile .copy {cursor: pointer;}
.hero__profile .backhomebtn {margin-left: 10%;}

.nav-tabs.righttab{float: right;margin-top: -7%;}

.collectionAction .btn-grad, .profiled .btn-grad, .item_details .btn-lg{ margin-right:10px;margin-bottom:20px;}

.item_details .share.active .dropdown__popup, .item_details .more.active .dropdown__popup { visibility: visible;opacity:1;}
.notificationerror p{text-align:center;}
.chooseContainer .sign__file{width:auto;height:180px;display:inline-block;text-align:center;margin:0 auto;}
.chooseContainer .sign__file button{padding:20px 30px;height:100%;background:#183b56;color:#fff;border:3px solid #325874;border-radius:30px;}
.chooseContainer .sign__file button:hover{border:3px solid #ffffff}
.chooseContainer .sign__file button::before{content:'';background: url(data:image/png;base64,) no-repeat bottom center/cover;width:80px;height:80px;display:block;margin:0 auto 20px auto;}
.uploadContainer{text-align:center;padding:40px;}
.uploadContainer li{width:40%;display:inline-block;padding:20px;}
.uploadContainer li a{border:3px solid #345a77;padding:50px 30px;border-radius:30px;margin:20px;width:100%;display:inline-block;}
.uploadContainer li a:hover{border:3px solid #ffffff}
.uploadSub h3{color:#ffffff;margin:15px 0;}
.RFS-StepMain span{color:#ffffff;}
.right_btn{width:100%;text-align:right;display:inline-block;}
.right_btn .btn{margin:0 10px;}

table.listView{width: 100%;margin-bottom:30px;}
table.listView tr th,table.listView tr td{text-align:center;color: #ffffff;padding: 5px;}
table.listView tr td .btn{margin:0 10px;}
.bottomButtons{text-align:center;}
.bottomButtons .btn{margin:0 10px;}
.errorResult{color: #ffffff;}
.errorResult h2{color: #ffffff;}
.RFS-StepButton.active{background-color: rgb(24 59 86);}
.item_stepper .RFS-StepButton.active{background-color: rgb(24 59 86);}
.item_stepper .RFS-StepButton{background-color:#5e5e5e;}
.sign__group .form-control, .sign__file .form-control{padding: 15px;border-radius: 20px;background-color: #ffffff;color: #232226;}
.footer__1 p,.footer__1 a,.footer__1 h2,.footer__1 h6,.footer__1 li{color:#ffffff;}
.walletPage{color:#ffffff;}
.walletPage .card-body{width:100%;}
.walletPage h3,.walletPage h4,.walletPage h5{color:#ffffff;}
.walletPage .input-group span{background:#183b56;vertical-align:middle;padding:15px 22px;border-bottom-right-radius:12px;border-top-right-radius:12px;cursor:pointer;}
.walletPage button{margin:0 10px;}
.walletBody{color:#ffffff;padding:20px;}
.walletBody h5,.walletBody h4,.walletBody a{color:#ffffff;}
.carousel .carousel-status,button.control-arrow.control-prev,button.control-arrow.control-next{display:none;}
.profileContainer .tabsContainer .btn-white{border: solid 1px #345a77!important;}
.hide{display: none;}
.errorMessage p{color:red;font-size:18px;margin:50px 0}
.successMessage p{color:#ffffff;font-size:24px;margin:50px 0}
.successnoteMessage p{color:#ffffff;font-size:17px;margin:50px 0}
aside.svelte-rntogh{z-index:999;}
.liveaction .main__link{margin:0;float: right;}
.item_details .itemdetailimg{height:auto !important; }
.slider-wrapper ul.slider .row.align-items-center a{display: inline-block;cursor: pointer;}
.faqContainer{width: 100%;display: inline-block;}
.faqContainer .faq-row-wrapper{background: transparent;}
.faqContainer .faq-row-wrapper .faq-title{width: 100%;display: inline-block;text-align: center;margin-bottom: 20px;border: none;}
.faqContainer .faq-row-wrapper .faq-title h2{color: #ffffff;}
.faqContainer .faq-row-wrapper .faq-body {box-shadow:0 17px 13px 0px #192c4b05;background-color: #183b56;padding:20px;border-radius:12px;}
.faqContainer .faq-row-wrapper .faq-body .faq-row{background: #0b1b27;padding:10px 20px;
    border-radius: 10px;border: none;margin-bottom: 20px;}
.faqContainer .faq-row-wrapper .faq-body .faq-row .row-title.closed,
.faqContainer .faq-row-wrapper .faq-body .faq-row .row-title.opening,
.faqContainer .faq-row-wrapper .faq-body .faq-row .row-title.opened,.faqContainer .faq-row-wrapper .faq-body .faq-row .row-title.expanded{}
.faqContainer .faq-row-wrapper .faq-body .faq-row .row-title-text{font-size:20px;font-weight:500;color:#ffffff}
.faqContainer .faq-row-wrapper .faq-body .faq-row .row-content[aria-expanded="true"]{height:100%!important;}
.faqContainer .faq-row-wrapper .faq-body .faq-row .row-content .row-content-text{font-size:16px!important;font-weight:500!important;color:#ffffff!important;border-top:1px solid #ccc;width: 100%;padding-top: 10px!important;margin-top: 10px!important;}
.faqContainer .faq-row-wrapper .faq-body .icon-wrapper{top:20px!important;}
.faqContainer .faq-row-wrapper .faq-body .icon-wrapper .arrow-image svg{fill: #ffffff!important;}


.liveactionlist .card_head > a:first-child {
    position: relative;
    height: 100%;
