}

.liveactionlist .card_head a span.countdown{
    position: absolute;
    z-index: 9;
    left: 10px;
    display: inline-block;
    margin-top: 10px;
    background: #fff;
    color: #000;
    padding: 0px 5px;
    border-radius: 4px;
    top: 8px;
}

.liveactionlist .card_head a .progress{
position: absolute;
    bottom: 0;
    width: 90%;
    margin: 0 auto;
    left: 0;
    right: 0;
    border-radius: 12px;
    bottom:20px;
    background-color: #ffffff;
    }

.liveactionlist .card_head a .progress .progress-bar{background-color:#5691fe;margin: 4px;
    border-radius: 4px;}

.timerdetail{position: relative;}
.timerdetail span.countdown{
    position: absolute;
    z-index: 9;
    left: 25px;
    display: inline-block;
    margin-top: 10px;
    background: #fff;
    color: #000;
    padding: 0px 5px;
    border-radius: 4px;
    top: 2px;
    font-size: 20px;
}
.search_result_right a {
  position: absolute;
  left: 0;
  z-index: 9;
  padding: 7px 30px 5px 50px;
  width: 100%;
  height: 100%;
}
.tab-content.item-tab-content p.gray-text {
  color: #a4b1bb;
}
.tab-content table.bidtable.table-border-new td,.tab-content table.bidtable.table-border-new th {
  border: 1px solid #ffffff !important;
}



